.wrapper__input {
    --border-radius: 3px;
    --border: 3px;
    --clear-width: 25px;

    position: relative;
    display: inline-flex;
}

.input {
    min-width: 300px;
    padding: 10px 15px;
    padding-right: calc(var(--clear-width) + 15px);
    border: var(--border) solid;
    border-color: var(--color-lightgray);
    border-radius: var(--border-radius);
    font-size: var(--font-size-medium);
    font-family: var(--font-family-main);
    outline: none;
}

.input:focus {
    border-color: var(--color-blue);
}

.clear {
    position: absolute;
    top: 50%;
    right: calc(var(--border) * 3);
    transform: translateY(-50%);
    width: var(--clear-width);
    height: var(--clear-width);
    object-fit: contain;
    object-position: center center;
    transition: 0.5s;
    opacity: 0.4;
    cursor: pointer;
    border: none;
    outline: none;
}

.clear__disabled {
    cursor: auto;
    opacity: 0.2;
}
